<template>
    <div class="payInfo">
        <Info :pool="pool" />
    </div>
</template>

<script>
import Info from '@/components/index/payInfo/info/Info'
export default {
    name:'payInfo',
    data () {
        return {
            pool:'',//订单信息
        }
    },
    components:{
        Info
    },
    created(){
        if(this.$store.state.actions == null || this.$store.state.actions == ''){
            $('body #app').html('')
            this.$router.push('/')
        }else{
            if(this.$route.params.type == 'dream'){
                console.log('小梦想互助')
            }else if(this.$route.params.type == 'trade'){
                console.log('小生意互助')
            }
            this.ord(this)
        }
    },
    methods:{
        // 准备下单
        ord(app){
            /**
             * @params app Vue实例对象
             */
            TD_Request('ds','ord',{action:JSON.stringify(app.$store.state.actions)},function(code,data){
                console.log(data)
                app.pool = DreamPoolAnalysis(data.pool);
                app.pool.order = data.actions.pay
            },function(code,data){
                if(code == 17){
                    app.$router.push('/');
                }
                console.log(data)
            })
        }
    }
}
</script>

<style lang="less" scoped>
    .payInfo{
        width: 100%;
        height: 100%;
        background: #fff;
    }
</style>
